<template>
<div>
    <div v-for="item in options" :key="item.key"  style="display: flex;align-items: center;">
        <el-button style="margin-right:20px;margin-bottom:10px">{{item.value}}</el-button>
        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange(item)">{{item.seleteAll}}</el-checkbox>
        <div style="margin: 15px 0;"></div>
        <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
            <el-checkbox v-for="city in item.cities" :label="city" :key="city">{{city}}</el-checkbox>
        </el-checkbox-group>

    </div>
</div>


</template>
<script>

  export default {
    data() {
      return {
        checkAll: false,
        checkedCities: ['上海', '北京'],
        cities: ['上海', '北京', '广州', '深圳'],
        isIndeterminate: true,
        options:[
            {key:'1',value:"热门城市",checkall:false,seleteAll:"全选",cities:['上海', '北京', '广州', '深圳']},
            {key:'2',value:"热门水果",checkall:false,seleteAll:"全选",cities:['苹果', '香蕉', '栗子', '李子']},
            {key:'3',value:"热门区域",checkall:false,seleteAll:"全选",cities:['龙华', '福田', '宝安', '龙岗']},
            {key:'4',value:"热门大山",checkall:false,seleteAll:"全选",cities:['汕头', '邵阳', '茂名', '湛江']},                                   
        ]
      };
    },
    methods: {
      handleCheckAllChange(item) {
          console.log("全选",item)
        // this.checkedCities = val ? item.cities : [];
        // this.isIndeterminate = false;
      },
      handleCheckedCitiesChange(value) {
          let citiesLength = this.options.map((item)=>{
              return item.cities.length
          })
        console.log("单选",value)
        let checkedCount = value.length;
        this.checkAll = checkedCount === citiesLength;
        this.isIndeterminate = checkedCount > 0 && checkedCount < citiesLength;
      },

    }
  };
</script>