<template>
  <div>
    <el-button @click="wordShow">word预览</el-button>
    <el-button @click="excelShow">excel预览</el-button>
    <el-button @click="pptShow">ppt预览</el-button>
    <el-card>
      <header>
        <el-button @click="pdfShow">pdf预览</el-button>
      </header>
      <el-button @click="changePdfPage(0)">上一页</el-button>
      <el-button @click="changePdfPage(2)">首页</el-button>
      <el-button @click="changePdfPage(1)">下一页</el-button>
      <span v-show="pageCount>0">{{currentPage}} / {{pageCount}}</span>
      <pdf
        class="pdf" 
        :src="pdfSrc"
        :page="currentPage"
        @num-pages="pageCount=$event"
        @page-loaded="currentPage=$event"
      ></pdf>

    </el-card>
      <!-- <div>
                    
            <object :data="pdf" type="application/pdf" width="100%" height="100%">
            
            <iframe :src="pdf" width="100%" height="100%" style="border: none;">
            
            This browser does not support PDFs. Please download the PDF to view it: <a :href="pdf">Download PDF</a>
            
            </iframe>
            </object>
      </div> -->
      <iframe :src="pdfSrc" width="100%" height="100%"></iframe>
  </div>
</template>
<script>
import pdf from "vue-pdf";

export default {
  components: {
    pdf
  },
  data() {
    return {
      pdfSrc: "",
      currentPage:1,
      pageCount:0,
      pdf:
        "https://cdn.shopify.com/s/files/1/0016/7755/7826/files/nonghang.pdf?5",
      word:
        "https://cdn.shopify.com/s/files/1/0016/7755/7826/files/text.docx?3",
      excel:
        "https://cdn.shopify.com/s/files/1/0016/7755/7826/files/text.xlsx?5",
      ppt: "https://cdn.shopify.com/s/files/1/0016/7755/7826/files/text.pptx?5"
    };
  },
  mounted() {},
  methods: {
    wordShow() {
      let wordUrl = this.word;
      window.open(
        "https://view.officeapps.live.com/op/view.aspx?src=" + wordUrl,
        "_self"
      );
    },
    excelShow() {
      let excelUrl = this.excel;
      window.open(
        "https://view.officeapps.live.com/op/view.aspx?src=" + excelUrl,
        "_blank"
      );
    },
    pptShow() {
      let pptUrl = this.ppt;
      window.open(
        "https://view.officeapps.live.com/op/view.aspx?src=" + pptUrl,
        "_blank"
      );
    },

    pdfShow() {
      this.pdfSrc = pdf.createLoadingTask(this.pdf);

    },
    changePdfPage(val){
        if(val === 0 && this.currentPage >1){
            this.currentPage--
        }else if(val ===1 && this.currentPage < this.pageCount){
            this.currentPage++
        }else if(val ===2 ){
            this.currentPage=1;
        }
        

    }
  }
};
</script>
<style lang="scss" scoped>
.pdf{
    display: block!important;
}
</style>